import { css } from '@emotion/css';
import { Colors } from '../colors';

export const layoutStyle = (disablePaddings: boolean, backgroundColor?: Colors): string => css({
  width: '100%',
  height: '100%',
  boxSizing: 'border-box',
  padding: disablePaddings ? '0' : '0 40px',
  backgroundColor,

  ['@media (max-width: 500px)']: {
    padding: disablePaddings ? '0' : '0 20px'
  }
});
