import { BehaviorSubject, Subscription } from 'rxjs';

export class FormControl<T> {
  private subscribtion: Subscription;

  readonly value$: BehaviorSubject<T>;
  readonly error$ = new BehaviorSubject('');
  readonly touched$ = new BehaviorSubject(false);

  constructor(private initialValue: T, validator: (value: T) => string) {
    this.value$ = new BehaviorSubject(this.initialValue);

    this.subscribtion = this.value$.subscribe((value: T) => {
      const error = validator(value);
      this.updateError(error);
    });
  }

  get value(): T {
    return this.value$.getValue();
  }

  update = (value: T): void => this.value$.next(value);

  touch = (): void => this.touched$.next(true);

  destroy(): void {
    this.subscribtion.unsubscribe();
  }

  clear(): void {
    this.touched$.next(false);
    this.value$.next(this.initialValue);
  }

  private updateError(error: string): void {
    this.error$.next(error);
  }
}
