import { FormControl, useRx } from 'common';
import { Text } from 'components';
import React from 'react';

export type TFormControlChildrenProps<T> = {
  onLeave?: VoidFunction;
  value?: T;
  onChange?: (value: T) => void;
};

type TFormControlFieldProps<T> = {
  control: FormControl<T>;
  children: React.ReactElement<TFormControlChildrenProps<T>>;
};

export function FormControlField<T>({
  control,
  children
}: TFormControlFieldProps<T>): JSX.Element {
  const value = useRx(control.value$);
  const error = useRx(control.error$);
  const touched = useRx(control.touched$);

  return (
    <div>
      {
        React.cloneElement(children,
          {
            ...children.props,
            value,
            onLeave: control.touch,
            onChange: control.update
          })
      }
      {Boolean(error) && touched && <Text color="red">{error}</Text>}
    </div>
  );
}

