import { UserSwipe } from 'common';
import { BehaviorSubject } from 'rxjs';
import { TUserChoiseItem, userDecisionStorageService } from './user-decision-storage.service';
import { syndicateTransport, TGetItemsModel, TItemModel } from '../transport';

export type TItems = Array<TItemModel>;

export type TUserDecisionServiceData = {
  data: TItems | void;
  error: Error | void;
};

class UserDecisionService {
  private static initialData: TUserDecisionServiceData = { data: void 0, error: void 0 };

  data$ = new BehaviorSubject<TUserDecisionServiceData>(UserDecisionService.initialData);

  getItems(): void {
    void syndicateTransport.getProjectsForSelection()
      .then((response: TGetItemsModel) => {
        const data = this.filterAlreadyChoosedItems(response.items);

        this.data$.next({ data, error: void 0 });
      })
      .catch((error: Error) => this.data$.next({ data: void 0, error }));
  }

  setItem(item: TItemModel, decision: UserSwipe): void {
    userDecisionStorageService.setItem(item, decision);
    const { data } = this.data$.getValue();

    if (data) {
      const filtered = data.filter((dataItem: TItemModel) => dataItem !== item);
      this.data$.next({ data: filtered, error: void 0 });
    }
  }

  private filterAlreadyChoosedItems(itemsFromResponse: TItems): TItems {
    const swipedItems = userDecisionStorageService.getItems();

    if (!swipedItems.length) {
      return itemsFromResponse;
    }

    return itemsFromResponse.filter((itemFromResponse: TItemModel) => swipedItems.every((swipedItem: TUserChoiseItem) =>
      swipedItem.photo !== itemFromResponse.photo.id && swipedItem.project !== itemFromResponse.project
    ));
  }
}

export const userDecisionService = new UserDecisionService();
