import { HeaderMenu, RoundedButton, Text, ButtonsContainer, Layout } from 'components';
import React from 'react';
import {
  contentStyle,
  explanationStyle,
  headerWrapperStyle,
  introPageStyle,
  isVisibleStyle
} from './intro-page.style';

enum IntroPageSteps {
  LOGO_STEP,
  CROSS_STEP,
  HEART_STEP,
  COOKIES_STEP
}

const stepsQueue = [
  IntroPageSteps.LOGO_STEP,
  IntroPageSteps.CROSS_STEP,
  IntroPageSteps.HEART_STEP,
  IntroPageSteps.COOKIES_STEP
];

const switchStepTimeout = 1000;

type TIntroPageProps = {
  onSetIntroViewed: VoidFunction;
};

export function IntroPage({ onSetIntroViewed }: TIntroPageProps): JSX.Element {
  const [steps, setStep] = React.useState<Array<IntroPageSteps>>([]);

  React.useEffect(() => {
    let timer: number | undefined;
    const nextStep = stepsQueue[steps.length];

    if (nextStep !== undefined) {
      timer = window.setTimeout(
        () => setStep((prevSteps: Array<IntroPageSteps>) => ([...prevSteps, nextStep])),
        switchStepTimeout
      );
    } else {
      timer && window.clearTimeout(timer);
    }

    return () => {
      timer && window.clearTimeout(timer);
    };
  }, [steps]);

  return (
    <Layout
      headerContent={(
        <div className={headerWrapperStyle(steps.includes(IntroPageSteps.LOGO_STEP))}>
          <HeaderMenu />
        </div>
      )}
      footerContent={(
        <ButtonsContainer>
          <div className={isVisibleStyle(steps.includes(IntroPageSteps.CROSS_STEP))}>
            <RoundedButton type="cross" />
          </div>
          <div className={isVisibleStyle(steps.includes(IntroPageSteps.HEART_STEP))}>
            <RoundedButton type="heart" />
          </div>
        </ButtonsContainer>
      )}
    >
      <div className={introPageStyle}>
        <div className={contentStyle}>
          <div className={explanationStyle}>
            <div className={isVisibleStyle(steps.includes(IntroPageSteps.CROSS_STEP))}>
              <Text size="l" type="span">cross = like</Text>
              {<span className={isVisibleStyle(steps.includes(IntroPageSteps.HEART_STEP))}>
                <Text size="l" type="span">,&nbsp;</Text>
              </span>}
            </div>
            <div className={isVisibleStyle(steps.includes(IntroPageSteps.HEART_STEP))}>
              <Text size="l" type="span">heart = dislike</Text>
            </div>
          </div>
          <div className={isVisibleStyle(steps.includes(IntroPageSteps.COOKIES_STEP))}>
            <Text size="l" color="red" onClick={onSetIntroViewed}>
            accept cookies to proceed
            </Text>
          </div>
        </div>
      </div>
    </Layout>
  );
}
