import { routerHistory, routerPaths } from 'common';
import { MainPage, ResultPage, StudioPage } from 'pages';
import React from 'react';
import { Router, Route, Switch } from 'react-router';


export function SyndicatePage(): JSX.Element {
  return (
    <Router history={routerHistory}>
      <Switch>
        <Route path={routerPaths.MAIN} exact component={MainPage} />
        <Route path={routerPaths.USER_CHOOSE_RESULTS} exact component={ResultPage} />
        <Route path={routerPaths.STUDIO} exact component={StudioPage} />
      </Switch>
    </Router>
  );
}
