export const hashedData = 'hashedData';

const reactRouterReplacing = `:${hashedData}`;

export const routerPaths = {
  MAIN: '/',
  USER_CHOOSE_RESULTS: '/results',
  STUDIO: '/studio'
};

export function getHashedUrl(data: string): string {
  return routerPaths.USER_CHOOSE_RESULTS.replace(reactRouterReplacing, data);
}
