import { introService } from 'common';
import React from 'react';
import { ChoosePage } from './choose-page';
import { IntroPage } from './intro-page';

export function MainPage(): JSX.Element {
  const [isIntroViewed, setIntroViewed] = React.useState(() => introService.getState());

  const handleSetIntroViewed = React.useCallback(() => {
    introService.setState(true);
    setIntroViewed(true);
  }, []);

  if (isIntroViewed) return <ChoosePage />;

  return <IntroPage onSetIntroViewed={handleSetIntroViewed} />;
}
