import { BehaviorSubject, combineLatest, Subscription } from 'rxjs';
import { FormControl } from '../utils';

const EMAIL_REGEXP = /^[\w.\-_а-яА-Я]+@[\w.\-_а-яА-Я]+\.[\wа-яА-Я]+$/;
const PHONE_REGEXP = /^[+]?[(]?[0-9]{3}[)]?[-\s.]?[0-9]{3}[-\s.]?[0-9]{4,6}$/im;

export class UserDataForm {
  private subscribtion: Subscription | void = void 0;

  readonly isFormValid$ = new BehaviorSubject<boolean>(false);

  readonly fields = {
    name: new FormControl<string>('', (name: string) => this.validateName(name)),
    contact: new FormControl<string>('', (contact: string) => this.validateContact(contact))
  };

  constructor() {
    this.subscribtion = this.validateForm();
  }

  destroy(): void {
    const fields = this.getFields();

    fields.forEach((field: FormControl<string>) => field.destroy());

    this.subscribtion?.unsubscribe();
  }

  private validateName(name: string): string {
    if (name.length === 0) return 'Enter your name';

    return '';
  }

  private validateContact(contact: string): string {
    if (contact.length === 0) return 'Enter your email or phone';

    if (contact.includes('@')) {
      const isEmailValid = EMAIL_REGEXP.test(contact);

      if (isEmailValid) return '';

      return 'Check your email is correct';
    }

    const isPhoneValid = PHONE_REGEXP.test(contact);

    if (isPhoneValid) return '';

    return 'Check your phone number';
  }

  private validateForm(): Subscription | void {
    const fields = this.getFields();

    return combineLatest(
      fields[0].error$, fields[1].error$
    ).subscribe(([fieldOneError, fieldTwoError]: [string, string]) => {
      this.isFormValid$.next(!fieldOneError && !fieldTwoError);
    });
  }

  private getFields(): Array<FormControl<string>> {
    return Object.values<FormControl<string>>(this.fields);
  }
}
