import { css, keyframes } from '@emotion/css';

const animationKeyframes = keyframes`
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
`;

export const loaderStyle = css({
  display: 'inline-block',
  position: 'relative',
  width: '80px',
  height: '80px',
  ['div']: {
    position: 'absolute',
    border: '4px solid #fff',
    opacity: 1,
    borderRadius: '50%',
    animation: `${animationKeyframes} 1s cubic-bezier(0, 0.2, 0.8, 1) infinite`,
    [':nth-child(2)']: {
      animationDelay: '-0.5s'
    }
  }
});
