import { useRx, TItems, userDecisionService, TItemModel, UserSwipe } from 'common';
import { ProcessingWrapper } from 'components';
import React from 'react';
import { ChoosePageContent } from './choose-page-content';

export function ChoosePage(): JSX.Element {
  const { error, data } = useRx(userDecisionService.data$);

  React.useEffect(() => userDecisionService.getItems(), []);

  const handleSelectItem = React.useCallback((item: TItemModel, select: UserSwipe) => {
    userDecisionService.setItem(item, select);
  }, []);

  return (
    <ProcessingWrapper error={error} data={data}>
      {(alreadyData: TItems) => <ChoosePageContent onSelectItem={handleSelectItem} itemsQueue={alreadyData} />}
    </ProcessingWrapper>
  );
}
