import { userFormService } from 'common';
import { FormControlField, Input, Text } from 'components';
import React from 'react';

export function UserForm(): JSX.Element {
  const { name, contact } = userFormService.form.fields;

  const handleSubmitForm = React.useCallback(() => {
    name.touch();
    contact.touch();
    userFormService.sendUserForm();
  }, [name, contact]);

  return (
    <div>
      <FormControlField control={name}>
        <Input placeholder="your name" />
      </FormControlField>
      <FormControlField control={contact}>
        <Input placeholder="your email or phone" />
      </FormControlField>
      <Text size="l" color="red" onClick={handleSubmitForm}>send</Text>
    </div>
  );
}
