import { fingerPrintService, syndicateTransport, userDecisionStorageService } from 'common';
import { BehaviorSubject } from 'rxjs';
import { UserDataForm } from './user-data.form';

class UserFormService {
  readonly form = new UserDataForm();
  readonly isRequestProcessing$ = new BehaviorSubject<boolean>(false);

  destroy(): void {
    this.form.destroy();
  }

  sendUserForm = (): void => {
    this.isRequestProcessing$.next(true);

    if (this.form.isFormValid$.getValue()) {
      void fingerPrintService.getFingerPrint().then((fingerPrint: string) => {
        const result = userDecisionStorageService.getItems();
        const name = this.form.fields.name.value;
        const contact = this.form.fields.contact.value;

        void syndicateTransport.setUserSelection({
          name,
          contact,
          result,
          fingerPrint
        }).finally(() => this.isRequestProcessing$.next(false));
      });
    }
  };
}

export const userFormService = new UserFormService();
