import { TItemModel, TItems, UserSwipe } from 'common';
import {
  ButtonsContainer,
  Detailled,
  HeaderMenu,
  Layout,
  RoundedButton
} from 'components';
import React from 'react';
import { choosePageStyle } from './choose-page.style';
import { AllItemsSwipedPage } from '../all-items-swiped-page';

type TChoosePageContentProps = {
  itemsQueue: TItems;
  onSelectItem: (item: TItemModel, decision: UserSwipe) => void;
};

export function ChoosePageContent({ itemsQueue, onSelectItem }: TChoosePageContentProps): JSX.Element {
  const firstItemInQueue = itemsQueue[0];

  const handleSelectItem = React.useCallback(() =>
    onSelectItem(firstItemInQueue, UserSwipe.SELECT), [firstItemInQueue, onSelectItem]
  );
  const handleRejectItem = React.useCallback(() =>
    onSelectItem(firstItemInQueue, UserSwipe.REJECT), [firstItemInQueue, onSelectItem]
  );

  // eslint-disable-next-line no-console
  const handleOpenDetailedInfo = React.useCallback(() => console.log(firstItemInQueue), [firstItemInQueue]);

  if (!firstItemInQueue) {
    return (
      <AllItemsSwipedPage />
    );
  }

  return (
    <Layout
      disablePaddings
      headerContent={<HeaderMenu />}
      footerContent={(
        <ButtonsContainer>
          <RoundedButton type="cross" onClick={handleRejectItem} />
          <RoundedButton type="heart" onClick={handleSelectItem} />
        </ButtonsContainer>
      )}
    >
      <div className={choosePageStyle(firstItemInQueue.photo.url)}>
        <div>
          <Detailled onOpenDetailedInfo={handleOpenDetailedInfo} projectName={firstItemInQueue.projectName} />
        </div>
      </div>
    </Layout>
  );
}
