import React from 'react';
import { linkStyle } from './link.style';

type TLinkProps = {
  size?: 'm' | 'l';
  color?: 'white' | 'black' | 'red';
  href: string;
  rel?: string;
  about?: string;
  children: React.ReactNode;
};

export function Link({ href, size = 'l', color = 'black', rel, about, children }: TLinkProps): JSX.Element {
  return (
    <a className={linkStyle(size, color)} href={href} rel={rel} about={about}>{children}</a>
  );
}
